.form {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 30px;

  // .form__file

  &__file {}

  // .form__btn

  &__btn {
    background-color: $mainColor;
    padding: 15px 30px;
    color: #fff;
    border-radius: 6px;
    align-self: flex-start;
    margin-top: 30px;
    cursor: pointer;
  }

  // .form__control-radio

  &__control-radio {
    input[type="radio"] {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      pointer-events: none;
    }
  }

  // .form__group

  &__group {
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 4px rgba($color: $mainColor, $alpha: .1);
    border: 1px solid rgba($color: $mainColor, $alpha: .1);
    padding: 20px;
    border-radius: 8px;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  // .form__controls-label

  &__controls-label {
    display: flex;
    align-items: center;
    line-height: 150%;
    margin-bottom: 6px;
    color: rgba($color: $mainColor, $alpha: .7);

    span {
      display: block;
      margin-right: 3px;
      color: $orange;
    }
  }

  // .form__controls

  &__controls {
    padding: 15px 20px;
    border: 1px solid rgba($color: $mainColor, $alpha: .1);
    border-radius: 4px;
    font-size: 14px;
    color: rgba($color: $mainColor, $alpha: .9);
    resize: none;

    &:focus,
    &:focus-visible,
    &:focus-within {
      outline: none;
      border-color: rgba($color: $mainColor, $alpha: .4);
    }
  }

}

.errorlist {
  margin-top: 10px;

  li {
    color: rgba($color: red, $alpha: .8);
    font-size: 14px;
  }
}